<script>
import Vue from "vue";
import axios from "axios";
import "@fortawesome/fontawesome-free/css/all.css";
import "@fortawesome/fontawesome-free/js/all.js";
import VueToast from "vue-toast-notification";
import "vue-toast-notification/dist/theme-sugar.css";
Vue.use(VueToast);
export default {
  name: "Choose",
  data() {
    return {
      showModal: false,
      name: "",
      email: "",
      message: "",
    };
  },
  methods: {
    sendMessage() {
      let valid = true;
      if (this.name == "" || this.email == "" || this.message == "") {
        valid = false;
      }

      if (valid) {
        const BOT_KEY = "1439691769:AAGStalTXM4VFt2GZ8vMGIF_mj6CzjdgxhI";
        const CHAT_ID = "-1001197351549";
        let msg_text = encodeURI(
          "<b>Name: </b>" +
            this.name +
            "\n<b>Email: </b> " +
            this.email +
            "\n<b>Message:</b> <i>" +
            this.message +
            "</i>"
        );
        axios
          .get(
            "https://api.telegram.org/bot" +
              BOT_KEY +
              "/sendMessage?chat_id=" +
              CHAT_ID +
              "&parse_mode=html&text=" +
              msg_text
          )
          .then((resp) => {
            if (resp.data.ok) {
              this.name = "";
              this.email = "";
              this.message = "";
              Vue.$toast.success("Success! We contact you soon.", {
                position: "bottom",
              });
            } else {
              Vue.$toast.error("Some error occures", { position: "bottom" });
            }
          })
          .catch((err) => {
            console.log(err);
            Vue.$toast.error(err);
          });
      } else {
        Vue.$toast.warning("Fill all fields, please.", { position: "bottom" });
      }
    },
  },
};
</script>
